export default {
	data() {
		return {
			oldpsd:'',
			newpsd:'',
			renewpsd:''
		}
	},
	computed:{
		isClick(){
			if(this.oldpsd.trim()&& this.newpsd.trim() &&this.renewpsd.trim()){
				return true;
			}else{
				return false;
			}
		}
	},
	methods: {
		check(){
			if(!(this.oldpsd.trim())){
				uni.showToast({
					title:'旧密码不能为空！',
					icon:'none'
				})
				return false
			}
			if(!(this.newpsd.trim())){
				uni.showToast({
					title:'输入新密码不能为空！',
					icon:'none'
				})
				return false
			}
			if(!(this.renewpsd.trim())){
				uni.showToast({
					title:'再次输入密码不能为空！',
					icon:'none'
				})
				return false
			}
			if(this.renewpsd.trim() !=this.newpsd.trim()){
				uni.showToast({
					title:'两次新密码输入不一致！',
					icon:'none'
				})
				return false
			}
			return true
		},
		updatePsd(){
			if(this.check()){
				console.log('ok')
			}
			
		}
	}
}
